import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
  CustomInput
  
} from 'reactstrap' 
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import React, { useState, useEffect } from 'react'
// import { viewcricketcontestprize, editcricketcontestprize } from '../../store/actions'
import { useSelector } from 'react-redux'
import '@styles/react/libs/flatpickr/flatpickr.scss'


const selectcategory = [

  { value: 'Fashion', label: 'Fashion' },
  { value: 'Coupon', label: 'Coupon' },
  { value: 'Electronics', label: 'Electronics' }
]
const VerticalForm = () => {
  
  // const dispatch = useDispatch()
  const store = useSelector(state => state.contestsprizes)
  const {view_cricket_contest_prize} = store
  console.log(view_cricket_contest_prize)
 
  const [editcontestprizeform, setEditcontestprizeform] = useState()
  
 
  useEffect(() => {
    // dispatch(viewcricketcontestprize)
  
  }, [view_cricket_contest_prize])
  useEffect(() => {
    setEditcontestprizeform({contest_id:view_cricket_contest_prize?.contest_id, name:view_cricket_contest_prize?.name, caption:view_cricket_contest_prize?.caption, type:view_cricket_contest_prize?.type, value:view_cricket_contest_prize?.value, prize_rank:view_cricket_contest_prize?.prize_rank, assets_url:view_cricket_contest_prize?.assets_url })
  }, [view_cricket_contest_prize])
  console.log(view_cricket_contest_prize?.contest_id)
  console.log(editcontestprizeform)
  const onSubmit = (e) => {
    e.preventDefault()
    console.log(editcontestprizeform)
    // dispatch(editcricketcontestprize(view_cricket_contest_prize.id, editcontestprizeform))
  }
  const onclear = (e) => {
    e.preventDefault()

  }

  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      
      fileReader.onload = () => {
        resolve(fileReader.result)
      }
      
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }

  const uploadImage = async (e) => {
    const file = e.target.files[0]
    const base64 = await convertBase64(file)
    setEditcontestprizeform({ ...editcontestprizeform, assets_url: base64 })
  }
  

  const editcategory = (e) => {
    //console.log(e.target.value)
    setEditcontestprizeform({...editcontestprizeform, type:e.value})
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Edit Football Contest Prizes</CardTitle>
      </CardHeader>
      <CardBody>
        <Form>
          <Row>
          <Col sm="12">
              <FormGroup>
                <Label for="EmailVertical">ID</Label>
                <Input
                  type="number"
                  name="Email"
                  id="EmailVertical"
                  value={editcontestprizeform?.contest_id}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, contest_id: e.target.value })}
                  placeholder="Enter No of ID "
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label>Name</Label>
                <Input
                  type="text"
                  name="name"
                  id="nameVertical"
                  value={editcontestprizeform?.name}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, name: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col>
              <FormGroup>
                <Label for="nameVertical">Caption</Label>
                <Input
                  type="text"
                  name="name"
                  id="nameVertical"
                  value={editcontestprizeform?.caption}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, caption: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col>
              <FormGroup>
                <Label for="nameVertical">Category</Label>
                <Select
                  theme={selectThemeColors}
                  className="react-select"
                  classNamePrefix="select"
                  defaultValue={selectcategory[0]}                 
                  onChange={(e) => editcategory(e)}
                  options={selectcategory}
                  isClearable={false}
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="EmailVertical">coins</Label>
                <Input
                  type="number"
                  name="Email"
                  id="EmailVertical"
                  value={editcontestprizeform?.value}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, value: e.target.value })}
                  placeholder="Enter No of Coins "
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="EmailVertical">Prize Rank</Label>
                <Input
                  type="number"
                  name="Email"
                  id="EmailVertical"
                  value={editcontestprizeform?.prize_rank}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, prize_rank: e.target.value })}
                  placeholder="Enter No of ID "
                />
              </FormGroup>
            </Col>
          
            <Col sm="12">
              <FormGroup>
                <Label for="exampleCustomFileBrowser">Custom File Input</Label>
                <CustomInput
                  type="file"
                  id="exampleCustomFileBrowser"
                  name="customFile"
                  onChange={(e) => uploadImage(e)}
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                  onClick={(e) => onSubmit(e)}
                >
                  Submit
                </Button.Ripple>
                <Button.Ripple
                  outline
                  color="secondary"
                  type="reset"
                  onClick={(e) => onclear(e)}
                >
                  Reset
                </Button.Ripple>
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </CardBody>
    </Card>
  )
}
export default VerticalForm
